import { Component, OnInit, NgModule } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { NgModel } from '@angular/forms';
import { FormsModule } from '@angular/forms';
import {MaterializeDirective, MaterializeAction} from 'angular2-materialize';
import { Recipe } from '../recipe.model';
import { RecipeService } from '../recipe.service';
import {slideInOutAnimation} from '../../animations/slide-in-out';
import {fadeInAnimation} from '../../animations/fadeInAnimation';
import {platformBrowserDynamic} from '@angular/platform-browser-dynamic';
import { Response} from '@angular/http';
import {BrowserModule} from '@angular/platform-browser';
import {MyRecipeService} from '../../myrecipe/myrecipe.service';
import {IRecipe} from '../../myrecipe/myrecipe.service';
import {DataStorageService} from '../../shared/data-storage.service';
import {AuthService} from "../../auth/auth.service";

declare var Materialize: any;
@Component({
  selector: 'app-recipe-detail',
  templateUrl: './recipe-detail.component.html',
  styleUrls: ['./recipe-detail.component.css'] ,
  animations: [fadeInAnimation]
})

export class RecipeDetailComponent implements OnInit {
  IRecipe: IRecipe[] = [];
  authenticated = false;
  recipe: Recipe;
  id: number;
  starsCount: number;
  constructor(private recipeService: RecipeService,
              private route: ActivatedRoute,
              private router: Router,
              private myrecipeService: MyRecipeService,
              private dataService: DataStorageService,
              private authService: AuthService) {
  }

  ngOnInit() {
    this.route.params
      .subscribe(
        (params: Params) => {

          this.id = +params['id'];
          this.recipe = this.recipeService.getRecipe(this.id);
          this.authenticated = this.authService.isAuthenticated();
        }
      );
  }

  onAddToShoppingList() {
    this.recipeService.addIngredientsToShoppingList(this.recipe.ingredients);
  }
  onAddToMyRecipes() {
    this.myrecipeService.addMyRecipes(<IRecipe>this.recipe);
    this.dataService.storeRecipesbyUID().subscribe(
      (response: Response) => {
      }
    );
}
  onEditRecipe() {
    this.router.navigate(['edit'], {relativeTo: this.route});
    // this.router.navigate(['../', this.id, 'edit'], {relativeTo: this.route});
  }

  onDeleteRecipe() {
    this.recipeService.deleteRecipe(this.id);
    this.router.navigate(['/recipes']);
  }
}



